import Vue from 'vue'
import Vuex from 'vuex'
import moment from "moment";
import { Toast } from "vant";




export type IRecord = {
    id?: string
    type: string
    output: number
    selectTag: string
    notes: string
    date: string
}



type State = {
    record: IRecord
    recordList: IRecord[]
    tagList1: {
        name: string;
        value: string;
    }[]
    tagList2: {
        name: string;
        value: string;
    }[]
}
const today = moment(new Date()).format('yyyy-MM-DD')
const resetRecord = {
    type: '-',
    output: 0,
    selectTag: '',
    notes: '',
    date: today
}
Vue.use(Vuex)
//id new Date().getTime().toString(16)
export default new Vuex.Store({
    state: {
        record: resetRecord,
        recordList: [],
        tagList1: [
            { name: '4', value: '' },
            { name: '5', value: '' },
            { name: '6', value: '' },
            { name: '7', value: '' },
            { name: '8', value: '' },
            { name: '9', value: '' },
            { name: '10', value: '' },
            { name: '11', value: '' },
            { name: '12', value: '' },
            { name: '13', value: '' }
        ],
        tagList2: [
            { name: '14', value: '' },
            { name: '15', value: '' },
            { name: '16', value: '' },
            { name: '17', value: '' },
            { name: '18', value: '' },
            { name: '19', value: '' },
            { name: '20', value: '' },
            { name: '21', value: '' },
            { name: '22', value: '' },
            { name: '13', value: '' }
        ]
    } as State,
    getters: {
        getType(state) {
            return state.record.type
        },
        getSelect(state) {
            return state.record.selectTag
        },
        getOutput(state) {
            return state.record.output
        },
        getNotes(state) {
            return state.record.notes
        },
        getDate(state) {
            return state.record.date
        },
        getRecord(state) {
            return state.record
        },
        getRecordList(state) {
            return state.recordList.length > 0 ?
                state.recordList : JSON.parse(window.localStorage.getItem('recordList') || '[]')
        }
    },
    mutations: {
        updateRecord(state, newRecord) {
            state.record = { ...state.record, ...newRecord }
        },
        saveRecord(state, count: string) {
            state.record = { ...state.record, output: parseFloat(count) }
            if (state.record.selectTag !== '') {
                const list = JSON.parse(window.localStorage.getItem('recordList') || '[]');
                state.recordList = (list.push({ ...state.record, id: new Date().getTime().toString(16) }), list)
                window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
                // window.alert('')
                Toast.success('')
            } else {
                // window.alert('~')
                Toast.fail(' ~')
            }
            state.record = resetRecord
        }
    },
    actions: {},
    modules: {}
})
